import { isEqualDefault } from '@ixfx/core';
import { SimpleEventEmitter } from '@ixfx/events';
import { toStringDefault } from '@ixfx/core';
/**
 * @internal
 */
export class MapOfMutableImpl extends SimpleEventEmitter {
    #map = new Map();
    groupBy;
    type;
    constructor(type, opts = {}) {
        super();
        this.type = type;
        this.groupBy = opts.groupBy ?? toStringDefault;
    }
    /**
     * Returns the type name. For in-built implementations, it will be one of: array, set or circular
     */
    get typeName() {
        return this.type.name;
    }
    /**
     * Returns the number of keys
     */
    get lengthKeys() {
        return this.#map.size;
    }
    /**
     * Returns the length of the longest child list
     */
    get lengthMax() {
        let m = 0;
        for (const v of this.#map.values()) {
            m = Math.max(m, this.type.count(v));
        }
        return m;
    }
    debugString() {
        const keys = [...this.#map.keys()];
        let r = `Keys: ${keys.join(`, `)}\r\n`;
        for (const k of keys) {
            const v = this.#map.get(k);
            if (v === undefined) {
                r += ` - ${k} (undefined)\r\n`;
            }
            else {
                const asArray = this.type.toArray(v);
                if (asArray !== undefined) {
                    r += ` - ${k} (${this.type.count(v)}) = ${JSON.stringify(asArray)}\r\n`;
                }
            }
        }
        ;
        return r;
    }
    get isEmpty() {
        return this.#map.size === 0;
    }
    clear() {
        this.#map.clear();
        super.fireEvent(`clear`, true);
    }
    addKeyedValues(key, ...values) {
        const set = this.#map.get(key);
        if (set === undefined) {
            this.#map.set(key, this.type.addKeyedValues(undefined, values));
            super.fireEvent(`addedKey`, { key: key });
            super.fireEvent(`addedValues`, { values: values });
        }
        else {
            this.#map.set(key, this.type.addKeyedValues(set, values));
            super.fireEvent(`addedValues`, { values: values });
        }
    }
    set(key, values) {
        this.addKeyedValues(key, ...values);
        return this;
    }
    addValue(...values) {
        for (const v of values)
            this.addKeyedValues(this.groupBy(v), v);
    }
    hasKeyValue(key, value, eq) {
        const m = this.#map.get(key);
        if (m === undefined)
            return false;
        return this.type.has(m, value, eq);
    }
    has(key) {
        return this.#map.has(key);
    }
    deleteKeyValue(key, value) {
        const a = this.#map.get(key);
        if (a === undefined)
            return false;
        return this.deleteKeyValueFromMap(a, key, value);
    }
    deleteKeyValueFromMap(map, key, value) {
        const preCount = this.type.count(map);
        const filtered = this.type.without(map, value);
        const postCount = filtered.length;
        this.#map.set(key, this.type.addKeyedValues(undefined, filtered));
        return preCount > postCount;
    }
    deleteByValue(value) {
        let something = false;
        [...this.#map.keys()].filter((key) => {
            const a = this.#map.get(key);
            if (!a)
                throw new Error(`Bug: map could not be accessed`);
            if (this.deleteKeyValueFromMap(a, key, value)) {
                something = true; // note that something was deleted
                // If key is empty, delete it
                if (this.count(key) === 0)
                    this.delete(key);
            }
        });
        return something;
    }
    delete(key) {
        const a = this.#map.get(key);
        if (a === undefined)
            return false;
        this.#map.delete(key);
        this.fireEvent(`deleteKey`, { key: key });
        return true;
    }
    firstKeyByValue(value, eq = isEqualDefault) {
        const keys = [...this.#map.keys()];
        const found = keys.find((key) => {
            const a = this.#map.get(key);
            if (a === undefined)
                throw new Error(`Bug: map could not be accessed`);
            const r = this.type.has(a, value, eq);
            return r;
        });
        return found;
    }
    count(key) {
        const entry = this.#map.get(key);
        if (entry === undefined)
            return 0;
        return this.type.count(entry);
    }
    /**
     * Iterates over values stored under `key`
     * An empty array is returned if there are no values
     */
    *get(key) {
        const m = this.#map.get(key);
        if (m === undefined)
            return;
        yield* this.type.iterable(m);
    }
    /**
     * Iterate over the values stored under `key`.
     * If key does not exist, iteration is essentially a no-op
     * @param key
     * @returns
     */
    *valuesFor(key) {
        const m = this.#map.get(key);
        if (m === undefined)
            return;
        yield* this.type.iterable(m);
    }
    getSource(key) {
        return this.#map.get(key);
    }
    *keys() {
        yield* this.#map.keys();
        //return Array.from(this.#map.keys());
    }
    *entriesFlat() {
        for (const entry of this.#map.entries()) {
            for (const v of this.type.iterable(entry[1])) {
                yield [entry[0], v];
            }
        }
    }
    *valuesFlat() {
        for (const entry of this.#map.entries()) {
            yield* this.type.iterable(entry[1]);
        }
    }
    *entries() {
        for (const [k, v] of this.#map.entries()) {
            const temporary = [...this.type.iterable(v)];
            yield [k, temporary];
        }
    }
    *keysAndCounts() {
        for (const key of this.keys()) {
            yield [key, this.count(key)];
        }
    }
    merge(other) {
        for (const key of other.keys()) {
            const data = other.get(key);
            this.addKeyedValues(key, ...data);
        }
    }
    get size() {
        return this.#map.size;
    }
    get [Symbol.toStringTag]() {
        return this.#map[Symbol.toStringTag];
    }
}
