import {} from '../types.js';
import { add, del, set, has } from './map-immutable-fns.js';
/**
 * Returns a {@link IMapMutable} (which just wraps the in-built Map)
 * Use {@link Maps.immutable} for the immutable alternative.
 *
 * @example Basic usage
 * ```js
 * const m = mapMutable();
 * // Add one or more entries
 * m.add(["name", "sally"]);
 * // Alternatively:
 * m.set("name", "sally");
 * // Recall
 * m.get("name");           // "sally"
 * m.delete("name");
 * m.isEmpty; // True
 * m.clear();
 * ```
 * @param data Optional initial data in the form of an array of `{ key: value }` or `[ key, value ]`
 */
export const mutable = (...data) => {
    // eslint-disable-next-line functional/no-let
    let m = add(new Map(), ...data);
    return {
        add: (...data) => {
            m = add(m, ...data);
        },
        delete: (key) => {
            m = del(m, key);
        },
        clear: () => {
            m = add(new Map());
        },
        set: (key, value) => {
            m = set(m, key, value);
        },
        get: (key) => m.get(key),
        entries: () => m.entries(),
        values: () => m.values(),
        isEmpty: () => m.size === 0,
        has: (key) => has(m, key),
    };
};
